.container {
  width: 500px;
  height: 220px;
  position: absolute;
  left: calc(50% - (90px));
  top: 100px;
  background: whitesmoke;
  box-shadow: 0 0 50px black;
  padding: 60px 30px;
  border-radius: 10px;
}

.dateBlock {
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
}

.dateBlock input:first-child {
  margin-right: 10px;
}

.dateBlock input {
  border-radius: 5px;
  padding: 5px;
  font-size: 20px;
}

.textBlock {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}

.textBlock input {
  border-radius: 5px;
  padding: 5px;
  font-size: 20px;
}
.btnBlock {
  display: flex;
  justify-content: center;
}

.btnAdd {
  padding: 5px 20px;
  border-radius: 5px;
  font-weight: bold;
  font-size: 20px;
}

.btnClose {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 5px;
  right: 5px;
  width: 20px;
  height: 20px;
  cursor: pointer;
  border: none;
}

.btnClose::before,
.btnClose::after {
  content: "";
  display: block;
  width: 20px;
  height: 2px;
  position: absolute;
  background: #7a7a7a;
}

.btnClose::before {
  transform: rotate(45deg);
}

.btnClose::after {
  transform: rotate(-45deg);
}

.btnClose:hover.btnClose::before,
.btnClose:hover.btnClose::after {
  background: black;
}

.btnClose:active.btnClose::before {
  transform: rotate(45deg) scale(0.7);
  box-shadow: 0 0 3px black;
}

.btnClose:active.btnClose::after {
  transform: rotate(-45deg) scale(0.7);
  box-shadow: 0 0 3px black;
}

.confirmBtn {
  position: absolute;
  top: 315px;
  left: 145.5px;
  padding: 5px 46px;
  background: #2f4ef6;
  cursor: pointer;
}

@media (max-width: 991px) {
  .container {
    width: 400px;
    left: calc(50% - (37px));
  }

  .dateBlock input {
    padding: 3px;
    font-size: 16px;
  }

  .textBlock input {
    border-radius: 5px;
    font-size: 14px;
  }
}

@media (max-width: 767px) {
  .container {
    left: calc(50% - (200px));
  }
}

@media (max-width: 480px) {
  .container {
    width: 300px;
    left: calc(50% - (150px));
  }
  .dateBlock input {
    font-size: 14px;
  }
  .btnAdd {
    font-size: 16px;
  }
}

@media (max-width: 380px) {
  .container {
    width: 270px;
    left: calc(50% - (135px));
  }
}
