.container {
  width: 500px;
  height: 130px;
  position: absolute;
  left: calc(50% - (90px));
  top: 100px;
  background: whitesmoke;
  box-shadow: 0 0 50px black;
  padding: 30px 30px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.question,
.buttons {
  display: flex;
  justify-content: center;
}

.question b {
  display: inline-block;
  min-width: 28px;
  text-overflow: ellipsis;
  margin-left: 5px;
  overflow: hidden;
}
.question span {
  display: block;
  flex-shrink: 0;
}

.button {
  padding: 5px 20px;
  border-radius: 5px;
  cursor: pointer;
}

.button:hover {
  background: #ccc;
}

.button:active {
  transform: scale(0.9);
}

.button:first-child {
  margin-right: 10px;
}

@media (max-width: 992px) {
  .container {
    width: 480px;
    left: calc(50% - (80px));
  }
}

@media (max-width: 900px) {
  .container {
    width: 380px;
    left: calc(50% - (30px));
  }
}

@media (max-width: 768px) {
  .container {
    width: 380px;
    top: 20%;
    left: calc(50% - (190px));
  }
}

@media (max-width: 480px) {
  .container {
    width: 340px;
    top: 20%;
    left: calc(50% - (170px));
  }
}

@media (max-width: 360px) {
  .container {
    width: 300px;
    top: 20%;
    left: calc(50% - (150px));
    font-size: 14px;
    padding: 30px 15px;
  }
}
