.todoItem {
  position: relative;
  display: flex;
  margin-bottom: 10px;
  justify-content: space-between;
}
.todoItem input {
  border-radius: 5px;
}
.textContainer {
  display: flex;
  text-overflow: ellipsis;
  overflow: hidden;
  margin-right: 10px;
}

.time {
  margin-right: 25px;
  letter-spacing: 1px;
  font-weight: 600;
}

input[type="time"] {
  flex-shrink: 0;
}

.text {
  text-overflow: ellipsis;
  overflow: hidden;
}

.textInput {
  display: block;
  flex-grow: 1;
  width: 125px;
  margin-right: 10px;
}

.buttons {
  display: flex;
}

.buttons img:first-child {
  margin-right: 3px;
}
.complitedText {
  text-decoration: line-through;
}

.complitedTime {
  opacity: 0.5;
}
